import { Card, CardBody } from "reactstrap"
import { ConfigPage } from "@macashipo/mlib"
export default function Page(props) {
  var configPage = ConfigPage.createFromConfigPageServer(props.configPage)
  console.warn("render redirect", props, configPage)
  if (configPage._config) {
    if (configPage._config["url"]) {
      window.location.href = configPage._config["url"]
    }
  }
  return (
    <Card className="col-12 col-sm-8 col-xl-6" style={{ margin: "0px auto" }}>
      <CardBody className="">Redirect...</CardBody>
    </Card>
  )
}
